.sidebar{
   display: flex;
   flex-direction: column;
   border-right: 0.5px solid rgb(230, 227,227);
   min-height: 100vh;
   background-color: #fff;
   .top{
      height: 50px;
      display: grid;
      align-items: center;
      // background-color: red;
      .logo{
         font-size: 20px;
         font-weight: bold;
         color: purple;
         padding-left:10px;
         text-transform: capitalize;
      }
   }
   hr{
      height: 0;
      border: 0.5px solid rgb(230, 227, 227);
   }
   .center{
      padding-left: 10px;
      ul{
         list-style: none;
         .title{
            font-size: 10px;
            font-weight: bold;
            color: #999;
            margin-top: 15px;
            margin-bottom: 5px;
         }
         li{
            display: flex;
            align-items: center;
            padding: 5px;
            cursor: pointer;
            &:hover{
               background-color: #ece8ff;
            }
            .icon{
               font-size: 18px;
               color: #7451f8;
            }
            span{
               font-size: 13px;
               font-weight: 600;
               color: #888;
               margin-left: 10px;
            }
         }
      }
   }
   .bottom{
      display: flex;
      align-items: center;
      margin: 10px;
      .colorOption{
         width: 20px;
         height: 20px;
         border: 1px solid #7451f8;
         margin: 5px;
         cursor: pointer;
         &:nth-child(1){
            background-color: whitesmoke;
         }
         &:nth-child(2){
            background-color: #333;
         }
      }
      
   }
}