.table{
   .cellWrapper{
      display: flex;
      align-items: center;
      .image{
         width: 32px;
         height: 32px;
         margin-right: 10px;
         border-radius: 50%;
         -o-object-fit: cover;
            object-fit: cover;
      }
   }
   .status{
      padding: 5px;
      border-radius: 5px;
      &.Approved{
         color: green;
         background-color: rgba(0, 128, 0, 0.151);
      }
      &.Pending{
         color: goldenrod;
         background-color: rgba(189, 189, 3, 0.103);
      }
   }
}