.new{
   width: 100%;
   display: flex;
   .newContainer{
      flex:6;

      .top, .bottom{
         box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, 0.47);
         padding: 10px;
         margin: 20px;
         display: flex;
         
         h1{
            color: lightgray;
            font-size: 20px;
         }
         .left{
            flex: 1;
            text-align: center;
            img{
               width: 100px;
               height: 100px;
               border-radius: 50%;
               -o-object-fit: cover;
                  object-fit: cover;
            }
         }
         .right{
            flex: 2;
            form{
               display: flex;
               flex-wrap: wrap;
               gap: 20px;
               justify-content: space-around;
               .formInput{
                  width: 40%;
                  input{
                     width: 100%;
                     padding: 5px;
                     border: none;
                     border-bottom: 1px solid gray;
                  }
                  label{
                     display: flex;
                     align-items: center;
                     gap: 10px;
                     .icon{
                        cursor: pointer;
                     }
                  }
               }
               button{
                  width: 150px;
                  padding: 10px;
                  border: none;
                  color: #fff;
                  background-color: teal;
                  font-weight: bold;
                  cursor: pointer;
                  margin-top: 10px;
               }
            }
         }
      }
   }
}